<template>
    <div class="ion-input" :class="[modeClass,{'clearInput':clearInput}]">

        <div class="input-innerWrap" @click="clickToFocus($event)">
            <input
                    :class="[textInputClass]"
                    :value="inputValue"
                    :type="typeValue"
                    :placeholder="placeholder"
                    :disabled="disabled"
                    :readonly="readonly"
                    :max="max"
                    :min="min"
                    :step="step"
                    ref="input"
                    @blur="inputBlurred($event)"
                    @focus="inputFocused($event)"
                    @input="inputChanged($event)"
                    @keydown="onKeydown($event)"
                    v-if="typeValue!=='textarea'">

            <textarea
                    :class="[textInputClass]"
                    :value="inputValue"
                    :placeholder="placeholder"
                    :disabled="disabled"
                    :readonly="readonly"
                    ref="textarea"
                    @blur="inputBlurred($event)"
                    @focus="inputFocused($event)"
                    @input="inputChanged($event)"
                    @keydown="onKeydown($event)"
                    v-if="typeValue ==='textarea'"></textarea>
        </div>

        <Button v-if="clearInput && typeValue!=='textarea' && hasValue()"
                clear
                class="text-input-clear-icon"
                @click="clearTextInput()"></Button>
    </div>
</template>
<style lang="scss">
    @import "./input.scss";
    @import "./input.ios.scss";
    @import "./input.md.scss";
</style>
<script type="text/javascript">
  /**
   * @component Input
   * @description
   *
   * ##  Input/Textarea
   *
   * ### 
   *
   * Inputtype : `text`,`password`, `email`, `number`, `search`, `tel`, and `url`. : `checkbox`, `radio`, `toggle`, `range`, `select`, etc. , input.
   *
   * , TextareaInput.
   *
   *
   *
   * ### 
   * ```
   * // 
   * import { Input, Textarea } from 'vimo/components/input'
   * // 
   * Vue.component(Input.name, Input)
   * Vue.component(Textarea.name, Textarea)
   * // 
   * export default{
   *   components: {
   *     Input, Textarea
   *  }
   * }
   * ```
   *
   *
   * ### 
   *
   * - blur
   * - `check`, check, 
   * - input, domsearch'ng-invalid', , .
   * - check, regex, .
   * - regex, check
   * - type: integer/positiveInteger/negativeInteger/mobile/email/ip/idCard
   *
   * ### type
   *
   *     |               | 
   * ------|-----------------|------------------------------------------------------------------------------
   *     | integer         |
   *    | positiveInteger |
   *    | negativeInteger |
   *     | email           |
   * IP  | ip              |
   *    | idCard          | 
   *     | password        | 6-18
   *   | tel             | XXXX-XXXXXXXXXXX- XXXXXXXXXXX-XXXXXXXXXX-XXXXXXXXXXXXXXXXXXXXXXX
   *  | mobile          | 13/14/15/18/17
   *   | cn              |
   *    | securityCode    | 4
   *     | nickName        | "-""_"
   * QQ  | qq              | qq: 1-95
   * URL | url             | URL, (https,http,ftp,rtsp,mms)
   *
   *
   *
   *
   *
   * @props {Boolean} [clearInput] - true, input, . textarea
   * @props {Boolean} [clearOnEdit] -  true, , typepasswordtrue, false, , js
   * @props {Boolean} [disabled] -  true, 
   * @props {Number} [max] -  , type=number
   * @props {Number} [min] -  , type=number
   * @props {Number} [step] - , type=number
   * @props {String} [mode='ios'] - 
   * @props {String} [placeholder] - 
   * @props {Boolean} [readonly] - , 
   * @props {String} [type='text'] - : "text", "password", "email", "number", "search", "tel", or "url"
   * @props {*} [value] - 
   * @props {Number} [debounce=0] - 
   * @props {RegExp} [regex] - 
   * @props {Boolean} [check] -  check, regex, , . check, regex, . check, check, , input, domsearch'ng-invalid', , .
   *
   *
   *
   * @fires component:Input#onBlur
   * @fires component:Input#onFocus
   * @fires component:Input#onInput
   *
   * @demo https://dtfe.github.io/vimo-demo/#/input
   * @usage
   * <Input placeholder="Text Input">
   * <Textarea @onBlur="onBlur($event)" @onFocus="onFocus($event)" @onInput="onInput($event)" placeholder="Enter a description"></Textarea>
   *
   * */
  import { hasFocus, setElementClass, isPresent, isFunction } from '../../util/util'
  import { Button } from '../../components/button'
  export default{
    data () {
      return {
        inputValue: this.value, // value
        typeValue: this.type, // type
        checkValue: this.check || this.regex, // check, 

        itemComponent: null, // item -> class
        inputElement: null, // , input/textarea

        timer: null,

        clearOnEditValue: this.clearOnEdit, // clearOnEdit, 
        didBlurAfterEdit: false, // clearOnEdit
        shouldBlur: true // 
      }
    },
    props: {
      /**
       * true, input, 
       * textarea
       * */
      clearInput: [Boolean],

      /**
       * true, , typepasswordtrue, false
       * , js
       * */
      clearOnEdit: [Boolean],

      /**
       * true, 
       * */
      disabled: [Boolean],

      /**
       * , type=number
       * */
      max: [Number],

      /**
       * , type=number
       * */
      min: [Number],

      /**
       * , type=number
       * */
      step: [Number],

      /**
       * 
       * */
      mode: {
        type: String,
        default () { return this.$config.get('mode') || 'ios' }
      },

      placeholder: [String],

      /**
       * , 
       * */
      readonly: [Boolean],

      /**
       * : "text", "password", "email", "number", "search", "tel", or "url"
       * */
      type: {
        type: String,
        default: 'text'
      },

      /**
       * 
       * */
      value: [String],

      debounce: {
        type: Number,
        default: 0
      },

      // 
      regex: [RegExp],

      // check, regex, , .
      // check, regex, 
      // check
      // check, 
      // input, domsearch'ng-invalid'
      // , .
      check: [Boolean]
    },
    computed: {

      modeClass () {
        return `input input-${this.mode}`
      },
      textInputClass () {
        return `text-input text-input-${this.mode}`
      }

    },
    methods: {
      /**
       * , ng-invalid, ng-valid
       * */
      verification () {
        if (!this.checkValue) return

        let result = this.getVerifyResult(this.inputValue, this.typeValue)
        if (result === null) return
        if (result) {
          this.itemComponent && setElementClass(this.itemComponent.$el, 'ng-valid', true)
          this.itemComponent && setElementClass(this.itemComponent.$el, 'ng-invalid', false)
        } else {
          this.itemComponent && setElementClass(this.itemComponent.$el, 'ng-valid', false)
          this.itemComponent && setElementClass(this.itemComponent.$el, 'ng-invalid', true)
        }
      },

      /**
       * 
       * @param {*} value - 
       * @param {String} type - 
       * @private
       * */
      getVerifyResult (value, type = 'text') {
        const regexps = this.$config.get('regexps')

        if (!value) {
          console.debug(', , true!')
          return null
        }

        let _regex = this.regex
        if (!_regex) {
          _regex = regexps[type]
        }

        // true, 
        if (!isPresent(_regex)) {
          console.debug('type:' + type + 'regex, , true!')
          return null
        }

        // 
        if (isFunction(_regex)) {
          return _regex(value)
        }

        // 
        let _tmpType = Object.prototype.toString.call(_regex).match(/^(\[object )(\w+)\]$/i)[2].toLowerCase()
        if (_tmpType !== 'regexp') {
          console.debug('regex:' + _regex + ', :' + _tmpType + ', , true!')
          return null
        }
        return _regex.test(value)
      },

      /**
       * , focus
       */
      clickToFocus () {
        this.setFocus()
      },

      /**
       * focus
       * */
      setFocus () {
        if (!hasFocus(this.inputElement)) {
          this.inputElement.focus()
        }
      },

      /**
       * blur
       * @private
       */
      inputBlurred ($event) {
        // debug: clearInputonBlur,blurclearInput, blur
        window.setTimeout(() => {
          if (this.shouldBlur) {
            // Item
            this.setItemHasFocusClass(false)

            /**
             * @event  component:Input#onBlur
             * @description blur
             * @property {object} $event - 
             */
            this.$emit('onBlur', $event)
            // clearOnEdit blurflag
            if (this.clearOnEditValue && this.hasValue()) {
              this.didBlurAfterEdit = true
            }

            // 
            this.verification()
          } else {
            this.shouldBlur = true
          }
        }, 16 * 2)
      },

      /**
       * focus
       * @private
       */
      inputFocused ($event) {
        // Item
        this.setItemHasFocusClass(true)
        this.setFocus()
        /**
         * @event  component:Input#onFocus
         * @description focus
         * @property {object} $event - 
         */
        this.$emit('onFocus', $event)
        this.itemComponent && setElementClass(this.itemComponent.$el, 'ng-touched', true)
      },

      /**
       * input, inputvalue(inputValue)
       * @private
       */
      inputChanged ($event) {
        this.inputValue = $event && $event.target ? $event.target.value : ''
        this.setItemHasValueClass()

        // debounce
        window.clearTimeout(this.timer)
        this.timer = window.setTimeout(() => {
          // 
          /**
           * @event  component:Input#onInput
           * @description input
           * @property {object} $event - 
           */
          this.$emit('onInput', $event)
          // v-model
          this.$emit('input', this.inputValue)
        }, this.debounce)
      },

      /**
       * 
       * */
      onKeydown () {
        if (this.clearOnEditValue) {
          this.checkClearOnEdit()
        }
      },

      checkClearOnEdit () {
        if (!this.clearOnEditValue) {
          return
        }

        // clearOnEdit,input
        if (this.didBlurAfterEdit && this.hasValue()) {
          this.inputValue = ''
          this.inputChanged()
        }

        // 
        this.didBlurAfterEdit = false
      },

      /**
       * 
       * */
      clearTextInput () {
        this.inputValue = ''
        this.inputChanged()
        this.shouldBlur = false

        this.setFocus()
        this.setItemHasFocusClass(true)
      },

      /**
       *  Itemclass
       */
      setItemHasFocusClass (isFocus) {
        if (this.itemComponent) {
          setElementClass(this.itemComponent.$el, 'input-has-focus', isFocus)
        }
      },

      /**
       *  Itemclass
       */
      setItemHasValueClass () {
        if (this.itemComponent) {
          setElementClass(this.itemComponent.$el, 'input-has-value', this.hasValue())
        }
      },

      /**
       * inputvalue
       * */
      hasValue () {
        const inputValue = this.inputValue
        return (inputValue !== null && inputValue !== undefined && inputValue !== '')
      }
    },
    created () {
      // textareatype=textarea
      if (this.$options._componentTag.toLowerCase() === 'textarea') {
        this.typeValue = 'textarea'
      }

      // , password, 
      if (this.type === 'password') {
        this.clearOnEditValue = true
      }
    },
    mounted () {
      // textareatype=textarea
      if (this.typeValue === 'textarea') {
        this.inputElement = this.$refs['textarea']
      } else {
        this.inputElement = this.$refs['input']
      }

      console.assert(this.inputElement, 'inputElement has undefined!')

      // item
      if (this.$parent.$options._componentTag.toLowerCase() === 'item') {
        this.itemComponent = this.$parent
        setElementClass(this.itemComponent.$el, 'item-textarea', (this.typeValue === 'textarea'))
        setElementClass(this.itemComponent.$el, 'item-input', true)
      }

      // ,value
      this.setItemHasValueClass()
    },
    components: {
      Button
    }
  }
</script>



