<template>
    <Page>
        <Header>
            <Navbar>
                <Title>Input</Title>
            </Navbar>
        </Header>
        <Content>

            <List>
                <ListHeader>
                    <span>Input</span>
                </ListHeader>
                <Item>
                    <Label color="primary">Inline Label</Label>
                    <Input placeholder="Text Input"></Input>
                </Item>
                <Item>
                    <Label color="primary" fixed>Fixed Label with fixed width</Label>
                    <Input type="tel" placeholder="Tel Input"></Input>
                </Item>
                <Item>
                    <Input type="number" placeholder="Number Input with no label"></Input>
                </Item>

                <Item>
                    <Label color="primary" stacked>Stacked Label</Label>
                    <Input type="email" placeholder="Email Input"></Input>
                </Item>

                <Item>
                    <Label color="primary" stacked>Stacked Label</Label>
                    <Input type="password" placeholder="Password Input"></Input>
                </Item>

                <Item>
                    <Label color="primary" floating>Floating Label</Label>
                    <Input></Input>
                </Item>

                <Item>
                    <Input placeholder="Clear Input" clearInput></Input>
                </Item>
            </List>

            <List>
                <ListHeader>
                    <span>Textarea</span>
                </ListHeader>
                <Item>
                    <Textarea @onBlur="onBlur($event)" @onFocus="onFocus($event)" @onInput="onInput($event)"
                              placeholder="Enter a description"></Textarea>
                </Item>
            </List>

            <List>
                <ListHeader>
                    <span>Textarea</span>
                </ListHeader>
                <Item>
                    <Textarea placeholder="Enter a description"></Textarea>
                </Item>
            </List>

            <List>
                <ListHeader>
                    <span></span>
                </ListHeader>
                <Item>
                    <Label color="primary">TestValue</Label>
                    <Input :debounce="0" :clearInput="true" placeholder="Text Input" v-model="testValue"
                           :disabled="testDisabled"></Input>
                </Item>
                <Item>
                    <span> : {{testValue}}</span>
                </Item>
                <Grid>
                    <Row justify-content-center>
                        <Button small outline @click="disableToggle">Disable Toggle</Button>
                    </Row>
                </Grid>
            </List>

            <List>
                <ListHeader>
                    <span></span>
                </ListHeader>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="'-''_'" type="nickName" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="integer" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="tel" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary">url</Label>
                    <Input placeholder="url" type="url" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="mobile" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="email" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="6-18" type="password" check></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="idCard" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="securityCode" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="cn" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary">QQ</Label>
                    <Input placeholder="QQ" type="qq" check clearInput></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="XX-XX-XXX" type="text" check :regex=/\d{2}-\d{2}-\d{3}/ clearInput></Input>
                </Item>
            </List>

            <List class="simSubmit">
                <ListHeader>
                    <span></span>
                </ListHeader>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="mobile" check @onBlur="checkValue"></Input>
                </Item>
                <Item>
                    <Label color="primary"></Label>
                    <Input placeholder="" type="password" check @onBlur="checkValue"></Input>
                </Item>
                <Grid>
                    <Row justify-content-center>
                        <p>: {{canSubmit}}</p>
                    </Row>
                    <Row justify-content-center>
                        <Button outline small :disabled="!canSubmit"></Button>
                    </Row>
                </Grid>
            </List>
        </Content>
    </Page>
</template>
<style lang="scss">
</style>
<script type="text/javascript">
  import { Label } from 'vimo/components/label'
  import { Input, Textarea } from 'vimo/components/input'
  import { List } from 'vimo/components/list'
  import { ListHeader, Item } from 'vimo/components/item'

  export default{
    name: 'name',
    data () {
      return {
        testValue: 'hello',
        testDisabled: false,
        canSubmit: false
      }
    },
    methods: {
      checkValue () {
        console.log('checkHandler')
        this.$nextTick(() => {
          this.canSubmit = document.querySelectorAll('.simSubmit .ng-valid').length === 2
        })
      },
      onBlur ($event) {
        console.debug('onBlur')
      },
      onFocus ($event) {
        console.debug('onFocus')
        console.debug($event)
      },
      onInput ($event) {
        console.debug('onInput')
      },
      disableToggle () {
        this.testDisabled = !this.testDisabled
      }
    },
    components: {Label, List, ListHeader, Item, Input, Textarea}
  }
</script>



